.header-color {
    background: #ff9966;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #ff5e62, #ff9966);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ff5e62, #ff9966); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
    background: #ff9966;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #ff5e62, #ff9966);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ff5e62, #ff9966); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    overflow: scroll;
}

.avatar-img {
    height: 300px;
    padding-top: 2%;
}

.banner-text {
    background-color: black;
    opacity: 0.8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    padding-bottom: 0px;
    margin-bottom: 0px;
    color: white;
}

.banner-text h2 {
    padding-top: 0px;
    margin-top: 0px;
    color: white;
}

.banner-text hr {
    border-top: 5px dashed white;
    width: 60%;
    margin: auto;
}

.banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
}

.social-links {
    padding-left: 35%;
    padding-right: 35%;
    width: 30%;
    display: flex;
    justify-content: space-between;
}

.social-links a {
    color: white;
    padding: 8px;
}